@mixin unit-spacing-variant($range-t, $range-r, $range-b, $range-l, $breakpoint){
  &.unit#{$breakpoint},
  &.unit#{$breakpoint}-vertical {
    > [class*='unit-']:first-child {
      padding-top: 0;
    }

    > .unit-left + .unit-right,
    > .unit-left + .unit-body {
      padding-left: 0;
      padding-top: $range-t;
    }

    > .unit-body + .unit-right {
      padding-left: 0;
      padding-top: $range-b;
    }
  }

  &.unit#{$breakpoint}-horizontal {
    > .unit-left + .unit-right,
    > .unit-left + .unit-body {
      padding-top: 0;
      padding-left: $range-l;
    }

    > .unit-body + .unit-right {
      padding-top: 0;
      padding-left: $range-r;
    }
  }

  //&.unit#{$breakpoint}-inverse {
  //  &,
  //  &.unit#{$breakpoint}-vertical {
  //    > [class*='unit-']:first-child {
  //      padding-top: $range-t;
