}

@mixin unit-spacing($medias, $range-t, $range-r:$range-t, $range-b:$range-t, $range-l:$range-r){
  @each $resolution, $alias in $medias {
    @if ($alias != '' and $alias != null) {
      $breakpoint: -#{$alias};
      @media (min-width: $resolution) {
        @include unit-spacing-variant($range-t, $range-r, $range-b, $range-l, $breakpoint);
      }
    } @else {
      $breakpoint: '';
      @include unit-spacing-variant($range-t, $range-r, $range-b, $range-l, $breakpoint);
    }
  }
}

@mixin unit-responsive($medias, $range-t, $range-r:$range-t, $range-b:$range-t, $range-l:$range-r) {
  .unit {
    @include display-flex();
    @include flex(0 1 100%);
    @include flex-direction(column);
  }

  .unit-body{
    @include flex(0 1 auto);
  }

  .unit-left,
  .unit-right{
    @include flex(0 0 auto);
    max-width: 100%;
  }

  @each $resolution, $alias in $medias {
    @if ($alias != '' and $alias != null) {
      $breakpoint: -#{$alias};
      @media (min-width: $resolution) {
        @include unit-variant($range-t, $range-r, $range-b, $range-l, $breakpoint);
      }
    } @else {
      $breakpoint: '';
      @include unit-variant($range-t, $range-r, $range-b, $range-l, $breakpoint);
    }
  }
}